.Main__outline{
    
    min-height: 500px;
    width: 500px;
    
    margin: auto;
    margin-top: 100px;
  

    background-color: #3f3160;
}



#todo-list{
    display: flex;
   
    margin-top: 20px;
    padding: 10px;
    padding-left: 15px;
    padding-top: 15px;
   
    
}

#todo-list  input{
    background-color:rgb(27,112,137);
   border: 0px;
   margin-left: 0px;
    height: 35px;
    width: 270px;
    border-radius: 8px;
    color: white;
    padding-left: 20px;
    letter-spacing: 1px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    opacity: 0.7;
 
   

}

#todo-list > input:focus{
    outline: none;
}

#todo-list input::placeholder{
    color: rgba(255,255,255,0.5);

}

.list{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #fff;;
}









