.list{
    display: flex;
    height: 30px;
    background-color: #bf07ac;
    
    
    align-items: center;
    margin-left: 40px;
    margin-right:50px;
    margin-top: 20px;
   padding-left: 30px;
    align-items: center;
   border-radius: 8px;
   color: white;
   height: auto;
   

 
  
} 

.list > p{

 align-items: center;
 padding-right:20px;
   
}

.list  i{
   
    text-align: right;
    margin-right: 10px;
    
    flex:1;   
    
}

.list i .icons{
    cursor: pointer;
   
}


.list > p >input{
    background-color: transparent;
    border: 0;
    color: white;
    font-size: 18px;
}

.list > p > input:focus{
    outline: none;
}
